<script>
import { ref, onMounted } from 'vue';
// eslint-disable-next-line import/extensions
import Intersect from '@/components/utils/intersect.js';

import PlaylistCard from '@/components/catalog/playlist/Card.vue';
import { getWTFPlaylists } from '@/api/catalog';

export default {
  components: {
    Intersect,
    PlaylistCard,
  },
  props: {
    filter: {
      type: Array,
      default: () => [],
    },
  },
  setup() {
    const isLoaded = ref(false);
    const limit = 8;
    const lastOffset = ref(0);
    const playlists = ref([]);
    const hasNext = ref(false);
    // eslint-disable-next-line no-shadow
    const fetchMedia = async (limit = 8, offset = 0) => {
      const { results } = await getWTFPlaylists(limit, offset);
      hasNext.value = true;
      playlists.value.push(...results);
    };
    const fetchNextPage = async () => {
      const offset = lastOffset.value + limit;
      await fetchMedia(limit, offset);
      lastOffset.value = offset;
    };
    onMounted(() => {
      fetchMedia();
    });
    return {
      isLoaded,
      playlists,
      hasNext,
      fetchNextPage,
    };
  },
};
</script>

<template>
  <div class="playlist-list">
    <div class="grid">
      <PlaylistCard
        v-for="playlist in playlists"
        :key="playlist.uid"
        :playlist="playlist"
      />
    </div>
    <intersect
      v-if="hasNext"
      @enter="fetchNextPage"
      class="load-more"
    >
      <span>loading</span>
    </intersect>
  </div>
</template>

<style lang="scss" scoped>
.playlist-list {
  margin: 0 0 8rem;
}
.load-more {
  min-height: 4rem;
  margin-top: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.grid {
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(4, 1fr);
}
</style>
